import React, { FC } from 'react';
import {
  boolColumn,
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { Cbo, CLASS_NAME } from '../../../../models/dtos/mfp/cbo';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'descricao',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const CboSearchView: FC<SearchViewProps<Cbo>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    boolColumn({
      dataField: 'professor',
      editor: true,
      filter: true,
      sort: true,
      text: 'Salário aula?',
    }),
    boolColumn({
      dataField: 'motoristaProfissional',
      editor: true,
      filter: true,
      sort: true,
      text: 'Motorista Profissional?',
    }),
  ];
  return (
    <WindowComponent title="Manutenção de CBO">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'CboEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default CboSearchView;
